import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    links: {},
    direction: false,
  },

  actions: {
    GetLinks({ commit }) {
      fetch("https://apis.handesmade.com/api/info")
        .then((response) => response.json())
        .then((data) => {
          commit("SET_LINKS", data.result);
        });
    },
  },
  mutations: {
    SET_LINKS(state, data) {
      state.links = data;
    },
    SET_DIRECTIONS(state, dir) {
      state.direction = dir;
    },
  },
  getters: {
    allLinks: (state) => state.links,
  },
  modules: {},
});
