import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./index.css";
import AOS from "aos";
import "aos/dist/aos.css";
import i18n from "./i18n";
import axios from "axios";
import VueAxios from "vue-axios";

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  i18n,
  axios,
  VueAxios,
  created() {
    AOS.init();
  },
  render: (h) => h(App),
}).$mount("#app");
