<template>
  <form
    class="w-full"
    data-aos="fade-right"
    data-aos-duration="1500"
    @submit.prevent="handleSubmit"
  >
    <p
      class="text-red-500 font-medium text-base mb-4 p-4 text-center"
      v-if="error"
    >
      {{ $t("form") }}
    </p>
    <div class="form__group">
      <input
        type="text"
        id="name"
        autocomplete="off"
        spellcheck="true"
        required
        class="form__control"
        v-model="name"
      />
      <label for="name" :class="`form__label ${dir ? 'right-6 left-0' : ''}`">{{
        dir ? "الاسم" : "Name"
      }}</label>
    </div>
    <div class="form__group">
      <input
        type="email"
        id="email"
        autocomplete="off"
        spellcheck="true"
        required
        class="form__control"
        v-model="email"
      />
      <label
        for="email"
        :class="`form__label ${dir ? 'right-6 left-0' : ''}`"
        >{{ dir ? "البريد الالكتروني" : "Email" }}</label
      >
    </div>
    <div class="form__group">
      <select
        id="subject"
        autocomplete="off"
        spellcheck="true"
        required
        class="form__control appearance-none"
        v-model="subject"
      >
        <option value="support">{{ dir ? "الدعم" : "Support" }}</option>
        <option value="suggession">{{ dir ? "اقتراح" : "Suggession" }}</option>
        <option value="other">{{ dir ? "اخرى" : "Other" }}</option>
      </select>
      <label
        for="subject"
        :class="`form__label ${dir ? 'right-6 left-0' : ''}`"
        >{{ dir ? "عنوان الرسالة" : "Your message subject" }}</label
      >
    </div>
    <div class="form__group">
      <textarea
        id="message"
        autocomplete="off"
        spellcheck="true"
        required
        class="form__control min-h-[150px]"
        v-model="message"
      ></textarea>
      <label for="message" :class="`form__label ${dir ? 'right-6 left-0' : ''}`"
        >{{ dir ? "الرسالة" : "Your message" }}
      </label>
    </div>
    <div class="form__group">
      <button class="submit__btn" aria-label="submit button" type="submit">
        {{ dir ? "ارسال" : "Send" }}
      </button>
    </div>
  </form>
</template>

<script>
// import axios from "axios";
export default {
  props: { dir: Boolean },
  name: "ContactForm",
  data() {
    return {
      name: "",
      email: "",
      subject: "",
      message: "",
      error: "",
    };
  },
  methods: {
    handleSubmit() {
      const self = this;
      const name = self.name;
      const email = self.email;
      const subject = self.subject;
      const message = self.message;
      const api = "https://apis.handesmade.com/api/contactUs";

      // email validation function to check email before send a message
      const emailValid =
        /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;

      // if statement check all fields are valid or not and send data via api
      if (
        name !== "" &&
        subject !== "" &&
        emailValid.test(email) &&
        message !== ""
      ) {
        const data = {
          name: name,
          email: email,
          subject: subject,
          msg: message,
        };
        console.log(data);
        fetch(api, {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((data) => {
            console.log("Success:", data);
          })
          .catch((error) => {
            console.error("Error:", error);
          });
        this.error = false;
      } else {
        this.error = true;
      }
    },
  },
};
</script>

<style>
.form__label {
  @apply absolute top-3 text-xl left-6 transition-all text-gray-400 table;
}
.form__control {
  @apply w-full
          border border-gray-400
          py-6
          px-6
          text-lg
          font-light
          rounded-xl;
}
.form__group {
  @apply w-full h-auto relative my-4;
}

.form__group > .form__control:valid + label,
.form__group > .form__control:focus + label {
  @apply text-base -top-3 bg-white px-2;
}
.form__group > .form__control:focus:invalid {
  @apply border-red-300 text-red-400;
}
.form__group > .form__control:focus:invalid + label {
  @apply text-red-400;
}
.submit__btn {
  box-shadow: 0 15px 50px 0 #7d93b250;
  @apply py-3 px-8 rounded-xl text-white font-semibold  transition-transform duration-300 ease-in-out bg-yellow-300 transform hover:-translate-y-1 w-60 table ml-auto;
}
</style>