<template>
  <div class="grid gap-6 lg:grid-cols-2 grid-cols-1">
    <div
      class="w-full h-auto p-4"
      v-for="(question, index) in questions"
      :key="index"
    >
      <h1
        class="
          text-2xl
          sm:text-3xl
          text-gray-500
          mb-4
          flex
          items-start
          justify-start
          cursor-pointer
        "
        @click="handleAnswer(index)"
      >
        <img
          :src="require(`@/assets/icons/${icons[index]}`)"
          alt="question icon"
          :class="`${direction ? 'ml-4' : 'mr-4'} self-center`"
          width="16"
          heihgt="17"
        />
        <span class="flex-1">{{
          direction ? question.question_ar : question.question_en
        }}</span>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          :class="`h-6 w-6 align-center hidden ${direction ? 'mr-4' : 'ml-4'}`"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          :data-minus="index"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M15 12H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          :class="`h-6 w-6 align-center ${direction ? 'mr-4' : 'ml-4'}`"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          :data-plus="index"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M12 9v3m0 0v3m0-3h3m-3 0H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
          />
        </svg>
      </h1>
      <p
        class="text-gray-400 text-base transition overflow-hidden h-0"
        :data-answer="index"
      >
        {{ direction ? question.answer_ar : question.answer_en }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    questions: [],
    icons: [],
    direction: Boolean,
    // a: true
  },
  methods: {
    handleAnswer(i) {
      const answer = document.querySelector(`[data-answer="${i}"]`);
      const plus = document.querySelector(`[data-plus="${i}"]`);
      const minus = document.querySelector(`[data-minus="${i}"]`);
      answer.classList.toggle("h-0");

      if (answer.classList.contains("h-0")) {
        plus.classList.remove("hidden");
        minus.classList.add("hidden");
      } else {
        plus.classList.add("hidden");
        minus.classList.remove("hidden");
      }
    },
  },
  name: "SingleQuestion",
};
</script>

