<template>
  <div id="app" class="flex flex-col min-h-screen">
    <Header :dir="dir" @ChangeDir="handleDirections" />
    <router-view :dir="dir" />
    <Footer :dir="dir" />
    <div
      class="
        fixed
        bottom-0
        left-0
        m-5
        flex
        items-center
        justify-center
        flex-col
      "
    >
      <div class="my-3 flex items-center justify-center flex-col">
        <div
          class="flex items-center justify-start flex-col mb-4"
          v-show="open"
        >
          <a
            :href="links.android_app"
            class="
              w-14
              h-14
              rounded-full
              flex
              items-center
              justify-center
              bg-gray-600
              text-2xl text-white
              my-1
            "
            type="button"
          >
            <i class="fab fa-google-play"></i>
          </a>
          <a
            :href="links.ios_app"
            class="
              w-14
              h-14
              rounded-full
              flex
              items-center
              justify-center
              bg-yellow-400
              text-2xl text-white
              my-1
            "
            type="button"
          >
            <i class="fab fa-apple"></i>
          </a>
        </div>
        <button
          class="
            w-14
            h-14
            rounded-full
            flex
            items-center
            justify-center
            bg-gray-700
            text-2xl text-white
          "
          type="button"
          @click="handleApps"
        >
          <i class="fas fa-mobile"></i>
        </button>
      </div>
      <a
        :href="`https://wa.me/${links.mobile}`"
        target="_blank"
        rel="noopener noreferrer"
        class="
          w-16
          h-16
          rounded-full
          flex
          items-center
          justify-center
          text-3xl text-white
          bg-[#128C7E]
          relative
        "
      >
        <span
          class="
            animate-ping
            absolute
            inline-flex
            h-full
            w-full
            rounded-full
            bg-[#128C7E50]
          "
        ></span>
        <i class="fab fa-whatsapp"></i>
      </a>
    </div>
  </div>
</template>

<script>
import Header from "./layout/Header.vue";
import Footer from "./layout/Footer.vue";
export default {
  data() {
    return {
      dir: false, // false as default value
      open: false,
    };
  },
  computed: {
    links() {
      return this.$store.getters.allLinks;
    },
  },
  methods: {
    CheckCurrentLang(lang) {
      if (lang === "ar") {
        this.dir = true;
        this.$store.commit("SET_DIRECTIONS", true);
      } else {
        this.dir = false;
        this.$store.commit("SET_DIRECTIONS", false);
      }
    },
    handleApps() {
      this.open ? (this.open = false) : (this.open = true);
    },
    handleDirections(val) {
      this.dir = val;
    },
  },
  created() {
    this.CheckCurrentLang(localStorage.getItem("locale") || "ar");
  },
  mounted() {
    this.$store.dispatch("GetLinks");
  },
  components: {
    Header,
    Footer,
  },
  name: "App",
};
</script>
